.modal {
  display: flex;
  flex-flow: column;
  align-items: center;
  justify-content: center;
}

.modal__title {
  font-size: 4rem;
  font-weight: 600;
}

.modal__icon svg {
  height: 10rem;
  fill: rgb(87, 196, 88);
}

.modal__description {
  font-size: 1.5rem;
  text-align: center;
}

