.button {
  border: none;
  color: white;
  margin: 1rem;
  cursor: pointer;
  font: inherit;
}

.blue {
  background-color: #2563eb;
  border: 1px solid #2563eb;
}

.gray {
  background-color: #9ca3af;
  border: 1px solid #9ca3af;
}

.outline {
  background-color: white;
}

.blue.outline {
  color: #2563eb;
}

.gray.outline {
  color: #9ca3af;
}

.normal {
  padding: 1rem 3rem;
  border-radius: 3rem;
}

.large {
  padding: 2rem 8rem;
  border-radius: 5rem;
  font-size: 2rem;
  margin-bottom: 2rem;
}

.center {
  justify-content: center;
}

.btn__wrapper {
  display: flex;
  flex-flow: row;
}

.modal {
  background-color: grey;
}
