.filter {
  display: flex;
  flex-flow: row;
  font: inherit;
  background-color: white;
  color: black;
  align-items: center;
  gap: 0.5rem;
  border-radius: 0.5rem;
  padding: 0.5rem 1rem;
  box-shadow: 0 1px 10px 0 rgba(0, 0, 0, 0.15);
}

.filter h6 {
  display: flex;
  flex-flow: row;
  align-items: center;
}

.filter h6 > * {
  margin-left: 0.3rem;
  margin-right: 0.3rem;
}

.filter__title {
  font-size: 1.5rem;
}

.filterSVG {
  height: 1.5rem;
}
