.graph {
  display: grid;
  text-align: center;
  color: black;
  background-color: white;
  padding: 2rem;
  border-radius: 2rem;
  box-shadow: 0 1px 10px 0 rgba(0, 0, 0, 0.15);
  margin-bottom: 5rem;
}

.yLabel {
  writing-mode: vertical-rl;
  text-orientation: mixed;
  transform: scale(-1);
}

.xAxis {
  border-top: 1px solid black;
}
.yAxis {
  border-right: 1px solid black;
}
