.average__item {
  display: flex;
  flex-flow: column;
  background-color: white;
  padding: 4rem 3rem 2rem 3rem;
  border-radius: 1.5rem;
  position: relative;
  justify-content: center;
  align-items: flex-end;
  box-shadow: 0 1px 10px 0 rgba(0, 0, 0, 0.15);
}

.average__info {
  display: flex;
  flex-flow: row;
}

.average__numbers {
  display: flex;
  flex-flow: row;
  align-items: flex-end;
}
.average__numbers h6 {
  margin-bottom: 0.3rem;
  margin-left: 0.3rem;
}
