.log__item {
  display: flex;
  flex-flow: row;
  padding: 1rem;
  justify-content: flex-start;
  align-items: center;
  border-radius: 1rem;
  background-color: white;
  box-shadow: 0 1px 10px 0 rgba(0, 0, 0, 0.15);
}

.log__figure img {
  height: 7rem;
  width: 7rem;
  border-radius: 50%;
}

.log__figure {
  margin: 1rem;
}

@media (max-width: 400px) {
  .log__figure {
    margin: 0 0.5rem 0 0;
  }
}

.log__details {
  display: flex;
  flex-flow: column;
  flex-grow: 1;
}

.log__chips {
  display: flex;
  flex-flow: row;
}

.log__chips > * {
  margin: 0.5rem 0.5rem;
}

.log__time {
  background-image: linear-gradient(to top left, #3f74e7, #82bafd);
  color: white;
  display: flex;
  flex-flow: row;
  border-radius: 1rem;
  justify-content: center;
  align-items: center;
}
