.selector {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.space-between {
  justify-content: space-between;
}

.label {
  margin-right: 3rem;
}

.select {
  font: inherit;
  padding: 1rem 3rem;
  border-radius: 1rem;
  border: 1px solid grey;
  -webkit-appearance: none;
}
